import React from 'react'
import { Avatar, Tooltip } from '@material-ui/core'
import {
  UilSmileBeam,
  UilExclamationCircle,
  UilMinusCircle,
} from '@iconscout/react-unicons'

export default function AnalyticsStatus({ active, anomaly = true, ...props }) {
  const Icon = active
    ? !anomaly
      ? UilSmileBeam
      : UilExclamationCircle
    : UilMinusCircle
  return (
    <Tooltip
      title={
        active
          ? !anomaly
            ? '–ü—Ä–æ–±–ª–µ–º –Ω–µ –æ–±–Ω–∞—Ä—É–∂–µ–Ω–æ'
            : '–ú—ã –æ–±–Ω–∞—Ä—É–∂–∏–ª–∏ –ø—Ä–æ–±–ª–µ–º—É!'
          : '–ê–Ω–∞–ª–∏—Ç–∏–∫–∞ –æ—Ç–∫–ª—é—á–µ–Ω–∞'
      }
    >
      <Avatar
        style={{
          color: `var(--color-${!anomaly ? 'success' : 'error'})`,
          background: `var(--color-${
            !anomaly ? 'success' : 'error'
          }-background)`,
          transition: 'all 200ms ease-in',
        }}
      >
        <Icon />
      </Avatar>
    </Tooltip>
  )
}
